import {
		mapState,  
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			team_list:'',
			page:1,
			Height:0,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			i1:0,
			i2:20,
			n:0
		}
	},
	methods: {
		lower(e) {
			this.page = this.page + 1
			let data = {
				user_id: this.userInfo.user_id,
				pageNo:this.page,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/underline/index',data,'GET').then(res => {
				if(!this.n) {
					uni.showLoading({
						title: '加载中...',
						mask: true
					})
						let list = res.data.retval
						if(res.data.retval.length < 1){
							this.n = 1
							uni.showToast({
								icon: 'none',
								title: '没有消息了'
							})
						}else{
							this.n = 0
							uni.showToast({
								icon: 'success',
								title: '加载成功'
							})
						}
						this.team_list = this.team_list.concat(list)
						uni.hideLoading()
				}
			})
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success:(res) => {
				this.Height = res.screenHeight - 44 +'px'
			}
		})
		let data = {
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/underline/index',data,'GET').then(res => {
			if(res.data.code === 1000) {
				this.team_list = res.data.retval
				if(this.team_list.length < 1) {
					this.n = 1
				}
				else{
					this.n = 0
				}
			}else {
				uni.showToast({
					title:"加载失败",
					icon:"none"
				})
			}
		})
	}
}
